@black:        #1a1a1a;
@blue:        #428bca;
@red:        red;

// assign styleguide
@link-color:        @blue;
@link-color-hover:  darken(@link-color, 10%);

// responsive utils

@breakpoints: {
    sm: 320px;
    md: 768px;
    lg: 1024px;
  };

  .breakpoint_lg(@rules){
    @media (min-width: @breakpoints[lg]) {
        @rules();
      }
  }

  @spacing: {
    sm: 1rem;
    md: 2rem;
    lg: 3rem;
  };

  .grid-loop(@columns, @total: @columns) when (@columns > 0) {
    // loop output
    .grid-@{columns} {
      width: percentage(@columns / @total);
    }
    
    // recursive call
    .grid-loop(@columns - 1, @total);
  }